@import url(https://fonts.googleapis.com/css?family=Montserrat:regular,bold,italic&subset=latin,latin-ext);
.gradient-border {
    --borderWidth: 2px;
    background: var(--dark-color);
    position: relative;
    border-radius: var(--borderWidth);
    z-index: 0;
    margin: 10px var(--borderWidth) 15px var(--borderWidth);
  }

.gradient-border:after {
    content: '';
    position: absolute;
    top: calc(-1 * var(--borderWidth));
    left: calc(-1 * var(--borderWidth));
    height: calc(100% + var(--borderWidth) * 2);
    width: calc(100% + var(--borderWidth) * 2);
    /* background: linear-gradient(60deg, #5073b8, #1098ad, #07b39b, #6fba82,  #f79533, #f37055, #ef4e7b, #a166ab, ); */
    background: linear-gradient(60deg, #5073b8, #1098ad, #07b39b,#1098ad,#5073b8);
    border-radius: calc(2 * var(--borderWidth));
    z-index: -1;
    -webkit-animation: animatedgradient 5s ease alternate infinite;
            animation: animatedgradient 5s ease alternate infinite;
    background-size: 250% 250%;
  }


@-webkit-keyframes animatedgradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


@keyframes animatedgradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}



* {
  padding: 0px;
  margin: 0px;
}

input,
textarea,
button,
select,
p,
a {
  -webkit-tap-highlight-color: transparent;
}

:root {
  --dark-color: #181818;
  --light-color: #f2f2f2;
}

.text {
  color: black;
  font-family: 'Montserrat';
  font-size: 18px;
}

.full-page {
  display: flex;
  width: 100vw;
  flex-direction: row;
  scroll-behavior: smooth;
}

.content {
  width: 100vw;
  transition: 500ms ease-out;
  border: none;
  outline: none;
  /* overflow: hidden; */
  height: 100%;
}

.row {
  display: flex;
  flex-direction: row;
}

@-webkit-keyframes fadeInOpacity {
  0% { opacity: 0 }
  100% { opacity: 1 }
}

@keyframes fadeInOpacity {
  0% { opacity: 0 }
  100% { opacity: 1 }
}

.fade-in {
  opacity: 1;
  -webkit-animation-name: fadeInOpacity;
          animation-name: fadeInOpacity;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.disable-selection {
  user-select: none;
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer */
  -khtml-user-select: none; /* KHTML browsers (e.g. Konqueror) */
  -webkit-user-select: none; /* Chrome, Safari, and Opera */
  -webkit-touch-callout: none; /* Disable Android and iOS callouts*/
}

body {
  height: 100%;
}
